.main-about-body {
  display: flex;
  justify-content: space-around;
  div {
    width: 50%;
    display: inline-block;
    img {
      width: 99.5%;
      height: 100%;
    }
  }
  div:nth-child(1) img {
    float: left;
  }
  div:nth-child(2) img {
    float: right;
  }
}

.main-about-footer {
  padding: 25px;
  background: #e1e3e5;
  p {
    font-size: 18px;
    color: #b3b4b4;
    margin: 0;
    padding: 0;
    text-align: left;
  }
  p:first-child {
    padding-bottom: 20px;
  }
}

.main-all-width {
  max-width: 1100px;
  margin: 30px auto 0;

}

@media only screen and (max-width: @screen-md) and(min-width: @screen-sm) {
  .main-team{
    height:auto!important;
  }
  .main-about-body {
    max-width: 100% !important;
  }

  .main-about-body div img {
    width: 100%;
  }

}

.main-team {
  margin: 30px auto;
  ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

  }
  li {
    width: 32%;
    display: inline-block;
    text-align: left;
    h2 {
      color: #e25111;
    }
    h3 {
      color: #999999;
    }
    span {

      display: block;
      overflow: hidden;

      img {
        width: 100%;
      }
    }
  }
}

.main-about-info {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  p {
    display: block;
    width: 80%;
    color: #333333;
    font-size: 16px;
  }
  li {
    display: flex;
    align-items: top;
    height: 15%;
    list-style: none;
    width: 100%;
    flex-wrap: wrap;
    span {
      width: 20%;
      height: 30px;

      display: inline-block;

    }
  }
  li:nth-child(1) {
    span {
      background: url("images/about1.png") no-repeat center center /contain;
    }
  }
  li:nth-child(2) {
    span {
      background: url("images/about2.png") no-repeat center center /contain;
    }
  }
  li:nth-child(3) {
    span {
      background: url("images/about3.png") no-repeat center center /contain;
    }
  }
}

.about-map {
  width: 100%;
  height: 100%;
}



@media only screen and(max-width: @screen-md) and(min-width: @screen-sm) {



  .BMap_stdMpZoom {
    top: 0 !important;
    left: 0;
  }
}

@media only screen and(max-width: @screen-md) {


}

.main-about-info-right {
  background: #e2e2e2;
}

.main-date {
  display: block !important;
}

.main-about-time {

  div {
    width: 100% !important;
  }
}

.main-about-date {
  width: 30% !important;
}

.main-about-datetime {
  width: 100% !important;
  display: flex !important;
  overflow: hidden;
}

.date-box {
  display: block !important;
  width: 70% !important;
  font-size: 16px;
}

@media only screen and(max-width: @screen-lg) and(min-width: @screen-md) {

  .main-head {
    margin: 0 10px;
  }

  .main-team {
    padding: 0 10px;
  }

  .main-about-date {
    width: 30% !important;
  }

  .main-about-info {
    li {
      height: auto;
    }
  }
}

@media only screen and(max-width: @screen-md) and(min-width: @screen-sm) {
  .date-box {
    width: 80% !important;
    .main-about-date {
      width: 20% !important;
    }
    .main-about-time {
      width: 80% !important;
    }
  }

  .main-about-map {
    min-height: 200px !important;
  }

  .main-about-info-right {
    min-height: 200px !important;
  }

  .main-padding {
    padding: 0 !important;
  }

  .main-head {
    margin: 0 10px;
  }

  .main-team {
    padding: 0 20px;
    h2 {
      margin: 15px 0;
      font-size: 16px;
    }
    h3 {
      margin: 15px 0 5px;
      font-size: 14px;
    }
  }

  .main-team li {
    width: 48% !important;
  }

  .main-about-footer p {
    font-size: 12px;
  }

  .main-about-info {

    li {
      height: auto;
    }

    p {
      font-size: 8px;
    }
    .date-box {
      font-size: 8px;
    }
  }

  .main-about-info li span {
    height: 20px;
  }

}

@media only screen and(max-width: @screen-sm) {
  .prevbtn {
    display: block!important;
  }

  .nextbtn {
    display: block!important;
  }

  .main-team {
    padding: 0 10px;
    position: relative;
    ul {
      height: 100%;
      position: relative;
      li {
        width: 100% !important;
        display: none;
        position: absolute;
      }
    }
    h2 {
      margin: 15px 0;
      font-size: 16px;
    }
    h3 {
      margin: 15px 0 5px;
      font-size: 14px;
    }
  }

  .main-head {
    padding: 0 10px;
  }

  .main-about-body {
    flex-wrap: wrap;
  }

  .main-about-info-right {
    width: 100% !important;
    height: 220px !important;
  }

  .main-about-info {

    li {
      height: auto;
    }

    p {
      font-size: 15px;
    }
    .date-box {
      font-size: 15px;
    }
  }

  .main-about-map {
    width: 100% !important;
    height: 220px !important;
  }

  .main-about-date {
    width: 40% !important;
  }

  .main-about-time {
    width: 60% !important;
  }

  .main-about-footer {
    padding: 10px;
  }

  .main-about-footer p {
    font-size: 14px;
  }
}

.main-about-map {
  overflow: hidden;
}

.main-padding {
  padding: 0 10px;
}

.main-team-display {
  display: block !important;
  position:static!important;
}

.prevbtn {
  width: 30px;
  height: 30px;
  display: none;
  background: url(images/prevbtn.png) no-repeat center center / contain;
  top: 30%;
  left: 5%;
  position: absolute;
  cursor: pointer;
}
.prevbtn:hover{
  opacity:0.8;
}
.nextbtn:hover{
  opacity:0.8;
}
.nextbtn {
  width: 30px;
  height: 30px;
  display: none;
  background: url(images/nextbtn.png) no-repeat center center / contain;
  top: 30%;
  right: 5%;
  position: absolute;
  cursor: pointer;
}
#dituContent{
  width:100%;
  height:100%;
}
.BMap_cpyCtrl {
  display:none!important;
}
.anchorBL{
  display:none!important;
}
.BMap_stdMpPan {
  display: none!important;
}
.BMap_stdMpZoom{
  width:100px!important;
}
.BMapLabel{
  padding: 0 !important;
  border:none!important;
}