.share {
  display: flex;
  align-items: center;
  justify-content: center;
  margin:20px auto;
  font-size:20px;
  span{
    white-space: nowrap;

  }
  a{
    font-size:20px!important;
    color:#66667d;
  }
}

.sharebox {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.article-content-header {
  font-size: 34px;
}

.article-content {
  color: #ababab;
  font-size: 18px;
  text-align: left;
  text-indent: 2rem;
}

.article-footer-btn {
  margin: 50px auto;
  background: #ffffff;
  a {
    color: #c3c3c3;
    text-decoration: none;
    cursor: pointer;
    width:100px;
    display:inline-block;
  }
}
