.article-list {
  text-align: left;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0;
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #dfdfdf;
    padding: 18px 0;
  }
  .article-info {
    width: 82.32%;
    margin: 0;
    padding: 0;
    a {
      text-decoration: none;
    }
    a:hover {
      h3 {
        color: #e25111;
      }
    }
    h3 {
      color: #333333;
      font-size: 22px;
      margin: 0 0 10px 0;
    }
    p {
      color: #ababab;
      font-size: 18px;
    }
  }
  .article-date {
    background: #e25111;
    color: #ffffff;
    margin: 0;
    padding: 10px 0;
    text-align: center;
    width: 12.5%;
    strong {
      font-size: 38px;
      margin: 0 auto;
    }
    p {
      font-size: 16px;
    }
  }
}

.article-list-banner {
  img {
    width: 100%;
  }
}

.article-list-banner {
  margin-bottom: 50px;
}

@media only screen and(max-width: @screen-lg) and(min-width: @screen-md) {
  .article-list-banner {
    padding: 0 10px;
  }

  .article-list {
    padding: 0 10px;
  }
}

@media only screen and(max-width: @screen-md) and(min-width: @screen-sm) {
  .article-list .article-info h3 {
    font-size: 16px;
  }

  .article-list .article-info p {
    font-size: 14px;
  }

  .article-list {
    padding: 0 10px;
  }

  .article-list .article-date strong {
    font-size: 26px;
  }

  .article-list .article-date p {
    font-size: 10px;
  }

  .article-list li {
    border-bottom: 1px dashed #dedede;
    padding: 15px 0;
  }
}

@media only screen and(max-width: @screen-mds) {
  .article-date {
    width: 27.66% !important;
  }

  .article-info {
    width: 63.33% !important;
  }

  .article-info {
    p {
      display: none;
    }
  }
}
@media only screen and(max-width:@screen-sm){
  .article-list .article-info h3{
    font-size:16px;
  }
  .article-list{
    padding:0 5px;
  }
  .article-list .article-date{
    padding: 5px 0!important;
  }
  .article-list .article-date strong {
    font-size: 26px;
  }

  .article-list .article-date p {
    font-size: 10px;
  }
  .article-list li {
    border-bottom: 1px dashed #dedede;
    padding: 10px 0;
  }
  .article-page{
    display:none;
  }
  .article-btn{
    display:block!important;
  }
}
.article-prev-btn{
  display:inline-block;
  width:35px;
  height:35px;
  background:url("images/articleprev.png") no-repeat center center /contain;
}
.article-next-btn{
  display:inline-block;
  width:35px;
  height:35px;
  background:url("images/articlenext.png") no-repeat center center /contain;
}
.article-btn{
  display:none;
  text-align:center;
  padding:30px;
}