
@font-face {
  font-family: 'iconfont';
  src: url('fonts/iconfont.eot');
  src: url('fonts/iconfont.eot?#iefix') format('embedded-opentype'),
  url('fonts/iconfont.woff') format('woff'),
  url('fonts/iconfont.ttf') format('truetype'),
  url('fonts/iconfont.svg#iconfont') format('svg');
}


.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.header-nav {
  position: absolute;
  z-index: 1;
  width: 100%;
  div:nth-child(1) {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    a {
      padding: 20px 0;
    }
  }
  ul {
    display: inline-block;
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      display: inline-block;

    }
  }
  a {
    color: #ffffff;
    text-decoration: none;
    cursor: pointer;
  }
  .hd-nav {
    .search-box-li {
      background: #ffffff;
      display: none;
      div {
        width: 94%;
        padding: 0;
        margin: 0;
      }
      input {
        width: 100%;
        padding: 8px;
        outline: none;
        border: 1px solid #bababa;
        border-radius: 5px;
      }
      .search-submit {
        position: absolute;
        right: 10%;
        top: 19px;
      }
      .search-exit {
        display: inline-block;
        width: 14px;
        height: 17px;
        background: url("images/exits.png") no-repeat center center /contain;
      }
    }
    li {
      padding: 2rem 1rem;
      a {
        padding-bottom: 1rem;
        font-size:18px;
      }
      a:hover {
        color: #e25111;
        border-bottom: 4px solid #e25111;

      }
      span {
        display: none;
      }
    }
  }
  ul:nth-child(3) {
    li {

      margin: .5rem;
    }
  }
}

.container {
  width: 100%;
  padding: 0;
}

.carousel-control .left, .carousel-control .right {
  background: none;
}

.carousel-indicators li {
  background: #8d8d8a;
  border: none;
}

